import React, {Component} from 'react';
import Notification from "./Notification/Notification";
import "./NotificationPopup.scss"
import {subscribe} from "event-subscription";
import {ADD_NOTIFCATION} from "../../constants";

class NotificationPopup extends Component {

    state = {notifications: []};

    noticeSubscription = null;

    componentDidMount() {

        this.noticeSubscription = subscribe(ADD_NOTIFCATION, this.addNotification)

    }


    addNotification = (notification) => {

        this.setState((state) => {
            return {...state, notifications: state.notifications.push(notification)}
        })

    };

    render() {

        const {notifications} = this.state;

        return (
            <div id="NotificationPopup">

                {Array.isArray(notifications) && notifications.map((notification, key) => {
                    return <Notification title={notification.title} type={notification.type} display={true}/>
                })}

                <Notification title={"Form Submitted!"}/>

            </div>
        );
    }
}

function mapStateToProps({}) {
    return {};
}

export default NotificationPopup;
